import cookie from 'js-cookie'
import Vue from 'vue'

const state = {
	userInfo: cookie.get('token') || '', // 从cookie中拿取token
	isSuccess: false
}

const getters = {
	getUserInfo (state) {
		return state.userInfo
	}
}

// 异步调用
// const actions = {
// 	setUserInfo ({ commit }, user) {
// 		commit('setUserInfo', user)
// 	},
// 	delUserInfo ({ commit }) {
// 		commit('delUserInfo')
// 	}
// }

// 同步调用
const mutations = {
	setUserInfo (state, user) {
		state.userInfo = user.token
		let userObj = {user: user}
		let userInfo = Vue.prototype.Encrypt(userObj)
		cookie.set('token', user.token, {expires: 15}) // 将token存入cookie 并保留半个月 {domain: '.gefuzi.com'}
		window.localStorage.setItem('qhdUser', userInfo)
	},
	delUserInfo (state, userInfo) {
		state.userInfo = ''
		window.localStorage.removeItem('qhdUser')
	},
	getWxLoction (state, value) {
		state.isSuccess = value
	}
}

export default {
	state,
	getters,
	// actions,
	mutations
}



