/**
 * 配置编译环境和线上环境之间的切换
 *
 * h5Url: web页面域名地址
 * baseUrl: api域名地址
 * routerMode: 路由模式
 * imgBaseUrl: 图片所在域名地址
 *
 */

let h5Url = ''
// let h5Url = 'http://192.168.1.79:15010'
let baseUrl = 'api'
// let baseUrl = 'http://192.168.1.79:15008'
let routerMode = 'hash'
let imgBaseUrl

if (process.env.NODE_ENV === 'development') {
	imgBaseUrl = 'http://cangdu.org:8001/img/'
} else if (process.env.NODE_ENV === 'production') {
	baseUrl = 'https://csqdsq.cnvp.com.cn'
	// imgBaseUrl = 'http://cangdu.org:8001/img/'
}

export {
	baseUrl,
	h5Url,
	routerMode,
	imgBaseUrl
}



