import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import { baseUrl, h5Url } from './config/env'
import './assets/less/main.less'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

import vueTouch from 'kim-vue-touch'



Vue.directive('focus', {

inserted: function (el) {

el.focus()
}
})


const history = window.sessionStorage;

history.removeItem('count')
history.removeItem('/')
let historyCount = history.getItem('count') * 1 || 0;
history.setItem('/', 0);





Vue.use(vueTouch)
Vue.use(app)
Vue.use(ToastPlugin)
Vue.use(WechatPlugin)
Vue.use(ConfirmPlugin)
Vue.use(AlertPlugin)


Vue.prototype.domainName = baseUrl
Vue.prototype.domainNameH5 = h5Url
Vue.config.productionTip = false

router.beforeEach((to, from, next) => {




store.commit('updateLoadingStatus', { isLoading: true })
/* 路由发生变化修改页面title */
if (to.meta.title) {
document.title = to.meta.title
}
const toIndex = history.getItem(to.path);
const fromIndex = history.getItem(from.path);

if (toIndex) {
if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
store.commit('UPDATE_DIRECTION', {direction: 'forward'})
} else {
store.commit('UPDATE_DIRECTION', {direction: 'reverse'})
}
} else {
++historyCount;
history.setItem('count', historyCount);
to.path !== '/' && history.setItem(to.path, historyCount);
store.commit('UPDATE_DIRECTION', {direction: 'forward'})
}
next()
})

router.afterEach(function (to) {
store.commit('updateLoadingStatus', { isLoading: false })
})

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>'
})



